import React, { useState } from 'react';

const switchStyle = {
  position: 'relative',
  display: 'inline-block',
  width: '40px',
  height: '20px',
  marginLeft: 'auto',
};

const sliderStyle = {
  position: 'absolute',
  cursor: 'pointer',
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  backgroundColor: '#ccc',
  transition: '.4s',
  borderRadius: '20px',
};

const sliderBeforeStyle = {
  position: 'absolute',
  content: '""',
  height: '16px',
  width: '16px',
  left: '2px',
  bottom: '2px',
  backgroundColor: 'white',
  transition: '.4s',
  borderRadius: '50%',
};

const Horario = ({ onClose }) => {
  const [mostrarEnCalendario, setMostrarEnCalendario] = useState(false);

  const toggleMostrarEnCalendario = () => {
    setMostrarEnCalendario(!mostrarEnCalendario);
  };

  return (
    <div style={{ paddingTop: '0px', backgroundColor: '#fff', minHeight: '100vh', boxSizing: 'border-box' }}>
      <div style={{ fontFamily: 'Arial, sans-serif', maxWidth: 600, margin: '0 auto', padding: '0 20px' }}>
        <header style={{ backgroundColor: '#f57c00', color: 'white', padding: '10px 15px', fontSize: 20, fontWeight: 'bold', display: 'flex', alignItems: 'center', justifyContent: 'center', position: 'relative' }}>
          <div style={{ cursor: 'pointer', fontSize: '24px', userSelect: 'none', position: 'absolute', left: '15px' }} onClick={onClose}>
            &larr;
          </div>
          <span>Horario</span>
        </header>
        <div style={{ marginTop: 20, borderBottom: '1px solid #ddd', paddingBottom: 10 }}>
          <p style={{ fontSize: 16, margin: 0 }}>Compartir horario</p>
        </div>
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginTop: 20 }}>
          <span style={{ fontSize: 14, color: '#333' }}>Mostrar en calendario</span>
          <label style={switchStyle}>
            <input
              type="checkbox"
              checked={mostrarEnCalendario}
              onChange={toggleMostrarEnCalendario}
              style={{ opacity: 0, width: 0, height: 0 }}
            />
            <span
              style={{
                ...sliderStyle,
                backgroundColor: mostrarEnCalendario ? '#f57c00' : '#ccc',
              }}
            >
              <span
                style={{
                  ...sliderBeforeStyle,
                  transform: mostrarEnCalendario ? 'translateX(20px)' : 'translateX(0)',
                }}
              />
            </span>
          </label>
        </div>
      </div>
    </div>
  );
};

export default Horario;
